import { StackHandler } from "@stackframe/stack";
import { stackServerApp } from "@/lib/stack";

export default function Handler(props: any) {
  return (
    <div className="min-h-screen flex items-center justify-center p-8">
      <StackHandler
        app={stackServerApp}
        fullPage={true}
      />
    </div>
  );
}
